<template>
  <div class="set-page">
    <foodHeaderNav title="设置" eventShow="false"></foodHeaderNav>
    <div class="set-list">
      <!-- <div class="set-item" @click="gotolinkSet">
        <span>链接设置</span>
        <i class="iconfont icon-jiantou1"></i>
      </div>-->
      <div class="set-item">
        <!-- <span>开单默认</span>
        <i class="iconfont icon-jiantou1"></i>-->
        <mt-radio title="开单默认" v-model="orderTypes" :options="options"></mt-radio>
      </div>
      <!-- <div class="set-item">
        <span>版本信息</span>
        <i class="iconfont icon-jiantou1"></i>
      </div>
      <div class="set-item">
        <span>预点单默认时间选择</span>
        <i class="iconfont icon-jiantou1"></i>
      </div>-->
    </div>
    <footTabs></footTabs>
  </div>
</template>
<script>
import footTabs from "../common/footTabs";
import foodHeaderNav from "../common/foodHeaderNav";
import { Radio } from "mint-ui";
export default {
  components: {
    footTabs,
    foodHeaderNav
  },
  data() {
    return {
      options: [
        {
          label: "预订单",
          value: "preOrder"
        },
        {
          label: "现做订单",
          value: "nowOrder"
        }
      ],
      orderTypes: "preOrder"
    };
  },
  created() {
    console.log(this.$route);
    if (localStorage.orderTypes) {
      this.orderTypes = localStorage.orderTypes;
    } else {
      localStorage.orderTypes = this.orderTypes;
    }
  },
  methods: {
    // gotolinkSet() {
    //   localStorage.setApi = "foodSet";
    //   this.$router.push("/foodSettingApi");
    // }
  },
  watch: {
    orderTypes(val) {
      localStorage.orderTypes = val;
    }
  }
};
</script>
<style lang="less" scoped>
.set-list {
  padding-left: 0.4rem;
  background-color: #fff;
  .set-item:last-child {
    border: none;
  }
  .set-item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid#EEEEEE;
    padding-top: 0.32rem;
    padding-bottom: 0.32rem;
    padding-right: 0.453rem;
    .mint-radiolist {
      display: flex;
      align-items: center;
    }
  }
}
</style>
<style lang="less">
.set-page {
  .set-list {
    .set-item {
      .mint-radiolist {
        .mint-radiolist-title {
          margin: 3px;
        }
        .mint-cell {
          min-height: auto;
          background: none;
          .mint-radio-input:checked + .mint-radio-core {
            background-color: #f6a129;
            border-color: #f6a129;
          }
          .mint-cell-wrapper {
            background: none;
          }
        }
      }
    }
  }
}
</style>





